.product {
  &__buttons {
    position: relative;
    margin-top: 32px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    border-radius: 12px;
    display: grid;
    overflow: hidden;
    width: 100%;
    @include screen(tablet) {
      margin-top: 60px;
    }
    @include screen(laptop) {
      margin-top: 103px;
    }
    &:before {
      content: '';
      position: absolute;
      height: 32px;
      width: 1px;
      background-color: #CACACA;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__gallery {
    margin-top: 106px;
    position: relative;
    @include screen(tablet) {
      margin-top: 0;
    }
    .slick-dots {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
      li {
        width: auto;
        height: auto;
        margin: 0;
        button {
          padding: 0;
          width: 8px;
          height: 8px;
          font-size: inherit;
          transition: background-color $transition-default;
          line-height: inherit;
          background-color: #F0F2F3;
          border-radius: 50%;
          &:before {
            display: none;
          }
        }
        &.slick-active {
          button {
            background-color: #010101;
          }
        }
      }
    }
    .slick-slide {
      &:active {
        cursor: grabbing;
      }
    }
    .card-slider {
      max-width: 492px;
      margin-left: auto;
      @include screen(laptop) {
        position: relative;
      }
      &-prev,
      &-next {
        cursor: pointer;
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: #F0F2F3;
        @include screen(laptop) {
          width: 40px;
          height: 40px;
        }
        svg {
          width: 14px;
          @include screen(laptop) {
            width: 24px;
          }
        }
      }
      &-arrows {
        display: none;
        position: absolute;
        bottom: 18px;
        z-index: 2;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        @include screen(tablet) {
          display: flex;
          transform: translateY(-50%);
          top: 50%;
          bottom: auto;
        }
      }
      &-gallery {
        .slick-slide {
          height: auto;
          & > div {
            height: 100%;
          }
        }
        .slick-track {
          display: flex;
          align-items: stretch;
        }
        .card-slider__image {
          display: flex !important;
          align-items: center;
          margin: 0 auto;
          height: 100%;
          width: 100%;
          img {
            position: relative;
            left: 50%;
            transform: translateX(-50%);
            max-height: 320px;
            @include screen(laptop) {
              max-height: 492px;
            }
          }
        }
      }
      &-thumbnails {
        margin-top: 24px;
        display: none;
        height: 100%;
        @include screen(laptop) {
          margin-top: 0;
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 88px;
        }
        .slick-list {
          max-width: 280px;
          margin: 0 auto;
        }
        .slick-slide {
          margin: 0 4px;
          @include screen(laptop) {
            margin: 4px 0;
          }
          & > div {
            display: flex;
            align-items: center;
            justify-content: center;
          }
          .card-slider__image {
            padding: 8px;
            width: 60px;
            height: 60px;
            display: flex;
            background-color: #F0F2F3;
            border-radius: 8px;
            margin: 0 auto;
            justify-content: center;
            align-items: center;
            width: 100%;
            @include screen(laptop) {
              width: 88px;
              height: 88px;
            }
            img {
              width: 100%;
              height: 100%;
              object-fit: contain;
              position: relative;
              left: 50%;
              transform: translateX(-50%);
            }
          }
        }
      }
    }
  }
  &__content {
    margin-top: 46px;
    @include screen(tablet) {
      margin-top: 0;
    }
  }
  &__container {
    @include screen(tablet) {
      gap: 46px;
      margin-top: 102px;
      grid-template-columns: 400px 1fr;
      display: grid;
      align-items: flex-start;
    }
    @include screen(laptop) {
      margin-top: 175px;
      padding-right: 108px;
      align-items: flex-start;
      gap: 100px;
      grid-template-columns: 656px 1fr;
    }
  }
  &__button {
    background-color: #F0F2F3;
    padding: 16px;
    transition: color $transition-default;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    @include screen(tablet) {
      font-size: 20px;
      line-height: 24px;
    }
    &.active {
      color: #194DE7;
    }
  }
  &__blocks {
    margin-top: 32px;
    @include screen(tablet) {
      margin-top: 36px;
    }
    @include screen(laptop) {
      margin-top: 55px;
    }
  }
  &__block {
    display: none;
    @include screen(tablet) {
      max-width: 70%;
    }
    @include screen(laptop) {
      max-width: 50%;
    }
    &.active {
      display: block;
    }
    h3 {
      font-weight: 500;
      color: #2C2C2C;
      font-size: 24px;
      line-height: 26px;
      @include screen(tablet) {
        line-height: 22px;
        font-size: 20px;
      }
      @include screen(laptop) {
        line-height: 31px;
        font-size: 28px;
      }
    }
    p {
      margin-top: 20px;
      font-size: 16px;
      font-weight: 400;
      color: #2C2C2C;
      line-height: 22px;
      @include screen(desktop) {
        font-size: 18px;
        line-height: 28px;
      }
    }
    &__items {
      margin-top: 20px;
      @include screen(tablet) {
        margin-top: 16px;
      }
      @include screen(laptop) {
        margin-top: 20px;
      }
    }
    &__item {
      padding: 24px 0;
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      border-bottom: 1px solid #C8C8C8;
      @include screen(tablet) {
        grid-template-columns: 250px 1fr;
        padding: 20px 0;
      }
      @include screen(laptop) {
        padding: 24px 0;
        grid-template-columns: 300px 1fr;
      }
      h4 {
        font-weight: 400;
        color: #2C2C2C;
        font-size: 12px;
        line-height: 20px;
        @include screen(laptop) {
          font-size: 16px;
          line-height: 22px;
        }
      }
      span {
        color: #2C2C2C;
        font-weight: 400;
        font-size: 12px;
        line-height: 20px;
        @include screen(laptop) {
          font-size: 16px;
          line-height: 22px;
        }
      }
    }
  }
  &__title {
    font-size: 24px;
    line-height: 26px;
    color: #2C2C2C;
    font-weight: 500;
    @include screen(tablet) {
      font-size: 20px;
      line-height: 22px;
    }
    @include screen(laptop) {
      font-size: 28px;
      line-height: 31px;
    }
  }
  &__subtitle {
    color: #2C2C2C;
    font-size: 16px;
    margin-bottom: 16px;
    line-height: 18px;
    font-weight: 400;
    @include screen(laptop) {
      font-size: 20px;
      line-height: 22px;
    }
  }
  &__colors, 
  &__models {
    margin-top: 28px;
    @include screen(laptop) {
      margin-top: 40px;
    }
  }
  &__price {
    color: #2C2C2C;
    font-size: 28px;
    line-height: 30px;
    margin-top: 32px;
    font-weight: 500;
    text-transform: uppercase;
    @include screen(tablet) {
      font-size: 24px;
      line-height: 26px;
      margin-top: 24px;
    }
    @include screen(laptop) {
      margin-top: 36px;
      line-height: 35px;
      font-size: 32px;
    }
  }
  &__submit {
    font-weight: 500;
    background: linear-gradient(89.82deg, #023BE4 -18.56%, #00D3AD 119.66%);
    width: 100%;
    text-align: center;
    padding: 16px;
    border-radius: 100px;
    margin-top: 24px;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 8px;
    color: $white;
    @include screen(laptop) {
      margin-top: 32px;
      line-height: 24px;
      font-size: 20px;
    }
  }
  &__labels {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px;
  }
  &__label {
    cursor: pointer;
    position: relative;
    span {
      display: block;
      position: relative;
      border-radius: 50%;
      overflow: hidden;
      width: 40px;
      height: 40px;
      @include screen(tablet) {
        height: 28px;
        width: 28px;
      }
      @include screen(laptop) {
        width: 44px;
        height: 44px;
      }
      &:before {
        content: '';
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        box-shadow: 0px 4px 4px 0px #00000033 inset;
        transition: transform $transition-default;
        @include screen(tablet) {
          height: 28px;
          width: 28px;
        }
        @include screen(laptop) {
          width: 44px;
          height: 44px;
        }
      }
      &:after {
        content: '';
        position: absolute;
        width: 40px;
        border-radius: 50%;
        opacity: 0;
        transition: opacity $transition-default;
        height: 40px;
        border: 2px solid #336DE9;
        transform-origin: center;
        @include screen(tablet) {
          height: 28px;
          width: 28px;
        }
        @include screen(laptop) {
          width: 44px;
          height: 44px;
        }
      }
    }
    &-black {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #2C2C2C;
        }
      }
    }
    &-white {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #FFFFFF;
        }
      }
    }
    &-blue {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #1471DD;
        }
      }
    }
    &-purple {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #9A41B9;
        }
      }
    }
    &-red {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #E14A4A;
        }
      }
    }
    &-green {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #279D64;
        }
      }
    }
    &-pink {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #ED8E8E;
        }
      }
    }
    &-gray {
      span {
        &:before {
          box-shadow: 0px 4px 4px 0px #0000001A inset;
          background-color: #F4F4F4;
        }
      }
    }
  }
  &__input {
    position: absolute;
    width: 0;
    height: 0;
    visibility: hidden;
    &:checked {
      & + span {
        &:after {
          opacity: 1;
        }
        &:before {
          transform: scale(0.75);
        }
      }
    }
  }
  &__tip {
    margin-top: 8px;
    font-size: 14px;
    line-height: 20px;
    color: #A4A4A4;
    font-weight: 400;
    @include screen(laptop) {
      line-height: 22px;
      font-size: 16px;
    }
  }
  &__list {
    position: absolute;
    background-color: #F0F2F3;
    padding: 0 16px 14px;
    border-radius: 0 0 12px 12px;
    width: 100%;
    left: 0;
    display: none;
    &.active {
      display: block;
    }
  }
  &__option {
    cursor: pointer;
    margin-top: 16px;
    font-size: 16px;
    font-weight: 600;
    line-height: 19px;
    transition: color $transition-default;
    &:hover {
      color: $color-blue;
    }
    &.active {
      color: $color-blue;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  &__selected {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 16px;
    background-color: #F0F2F3;
    border-radius: 12px;
    cursor: pointer;
    @include screen(laptop) {
      padding: 14px 16px;
    }
    &.active {
      border-radius: 12px 12px 0 0;
      svg {
        transform: rotate(180deg);
      }
    }
    span {
      font-size: 14px;
      line-height: 20px;
      font-weight: 600;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 19px;
      }
    }
    input {
      position: absolute;
      width: 0;
      height: 0;
      visibility: hidden;
    }
    svg {
      transition: transform $transition-default;
    }
  }
  &__select {
    position: relative;
    margin-top: 16px;
  }
}